






























































































































import { Component, Vue } from 'vue-property-decorator'
import { getArticles, ArticleStateEnum, postHotTop, postArticleState } from '@/api/articles'
import { IArticleData, ITag } from '@/api/types'
import Pagination from '@/components/Pagination/index.vue'
import { Form, Popover } from 'element-ui'
import { CatagoryDropdown } from './components/Dropdown'
import { dateformatter } from '@/utils/validate'
@Component({
  name: 'ArticleList',
  filters: {
    statusFilter:(status: number)=> {
      const statusMap: { [key: string]: string } = {
        草稿: 'success',
        发布: 'gray',
        deleted: 'danger'
      }
    
      return statusMap[ArticleStateEnum[status]]
    }

  },
  components: {
    CatagoryDropdown,
    Pagination
  }
})
export default class extends Vue {
  private total = 0
  private list: IArticleData[] = []
  private listLoading = true
  private listQuery = {
    page: 1,
    size: 20,
    catagoryId: null,
    dateRange:''
  }
  private popVisible:boolean=false
  private hotTop: number=0

  created() {
    this.getList()
  }
  
  parseTime(timestamp: string) {
    return dateformatter(new Date(timestamp),'yy-MM-dd hh:mm')
      //return new Date(timestamp).toISOString()
  }
  private async getList() {
    this.listLoading = true
    var searchOptions: any = {
      columns: [
        { name: 'catagoryId', search: { value: this.listQuery.catagoryId, Opeartor: 1 } },
        { name: 'createDate', search: { valueMin: this.listQuery.dateRange.length > 0 ? this.listQuery.dateRange[0] : '', valueMax: this.listQuery.dateRange.length > 1? this.listQuery.dateRange[1] : '', Opeartor: 9 } },
      ],
      order: [],
      pageIndex: this.listQuery.page,
      pageSize:this.listQuery.size
    }
    const { data } = await getArticles(searchOptions)
   
    this.list = data.data
    this.total = data.totalCount
    // Just to simulate the time of the request
    setTimeout(() => {
      this.listLoading = false
    }, 0.5 * 1000)
  }
  private showStatus(state: number):string {
    return ArticleStateEnum[state]
  }

  private resetForm(formName:string) {
    (this.$refs[formName] as Form).resetFields();
  }

  private async onSubmit() {
      await this.getList()

  }

  private async showPopover(row: any) {
    this.hotTop=row.hotTop
  }
  private async popConfirm(el: any, row: any) {
   
    await postHotTop({ aid: row.id, hotTop: this.hotTop })
    row.hotTop = this.hotTop
    this.$notify({
      title: 'Success',
      message: 'The post hotTop successfully',
      type: 'success',
      duration: 2000
    })
    el.doClose()
  }

  private async setArticleState(row: any, state: number) {
    await postArticleState({ aid: row.id, state: state})
    row.state = state
    this.$notify({
      title: 'Success',
      message: 'The post Article state successfully',
      type: 'success',
      duration: 2000
    })
  }
}
